.headerdiv {
    position: fixed;
    top: 0;
    width: 100%;
    height: 46px;
    background-color: black;
    z-index: 200;
}

.menu {
    height: 46px;
    /*display: inline-block;*/
    margin: auto;
}

.navbardiv {
    display: inline-block;
    margin: auto;
    width: fit-content;
    height: 100%;
}

.navbarlink {
    display: inline-block;
    position: relative;
    padding: 11.5px 20px 11.5px 20px;
    font-size: 19.2px;
    font-weight: bold;
    color: #FFFD38;
    font-family: 'Barrio', cursive;
    text-decoration: none;
}

@media (max-width: 1100px) {
    .menu {
        display: none;
    }
}

@media (max-width: 500px) {
    .headerdiv {
        width: 100%;
        height: 33px;
    }

    .menu {
        display: none;
    }


}