.maindiv {
    width: 100vw;
    height: 100vh;
    background-image: url("SiteBG.jpg");
    background-size: 200%;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
}

.logodiv {
    display: inline-block;
    padding-top: 35px;
    margin: 50px auto -140px auto;
    width: 90vw;
    height: 90vh;
    /*background-image: url("bottle.gif");*/
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    z-index: 100;
}

.videodiv {
    position: relative;
    margin: 10px auto -400px auto;
    width: 90vw;
    height: 90vh;

}

.video {
    margin: auto auto -400px auto;
    height: 100%;
}

.logo {
    position: absolute;
    top: 5px;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: url("SiteLogo.png");
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    width: auto;
    z-index: 100;

}

.scrolldiv {
    margin-top: -140px;
    margin-bottom: -410px;
    width: 100%;
    height: 3400px;
    background-image: url("HomepageBG1_3-min.png");
    background-size: cover;
}

.storydiv {
    position: fixed;
    top: 600px;
    display: inline-block;
    position: relative;
}

.story {
    font-size: 40px;
    font-weight: bold;
    color: #FFFD38;
    font-family: 'Barrio', cursive;
}

.lowerdiv {
    margin-top: 0px;
    width: 100%;
    height: 2148px;
    background-image: url("HomepageBG2_2.png");
    background-size: cover;
}

.video {
}


@media (max-width: 1060px) {
    .maindiv {
        margin-top: -5px;
    }

}

@media (max-width: 500px) {
    .maindiv {
        max-width: fit-content;
        margin-top: -5px;
        width: 100%;
        height: 50vh;
        background-image: url("SiteBG.jpg");
        background-position: top;
        background-size: 220%;
    }

    .logodiv {
        width: 100vw;
        height: 50vh;
        margin: 80px auto -100px auto;
        background-size: 70%;
        background-position: top;
    }

    .videodiv {
        background-position: top;
        margin: auto;
        width: 100%;
        height: 100%;

    }

    .video {
        max-width: 100%;
        margin-top: -60px;
        height: 90%;
    }

    .logo {
        position: absolute;
        background-size: 90%;
        top: 20px;
        background-repeat: no-repeat;
        background-position: top;
    }

    .scrolldiv {
        margin-top: -60px;
        margin-bottom: -410px;
        width: 100%;
        height: 1600px;
        background-image: url("HomepageBG1_3-min.png");
        background-position: top;
    }
}